        margin-top: 1%;
        width: 100%;
        height: 90%;
    }


    select {
        padding: 0 5px;
        border-bottom: 2px solid #696969;
    }

    .generate-button {
        padding: 10px;
        cursor: pointer;
        font-size: 20px;
        background: #5ebd6d;
        color: #ffffff;
        border: none;
        border-radius: 2px;
    }


    #pdf-wrapper {
        /*position: absolute;*/
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 0.1%;
        padding-top: 2%;
        background: white;
        flex-direction: column;
    }

    #pdf-wrapper h1, h2, h3, h4, h5 {
        font-family: "Segoe UI";
    }

    .pdf-header {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        /*background: red;*/
    }

    .pdf-header > img {
        width: 99%;
        height: auto;
        margin-left: -50px;
    }

    .pdf-page-content {
        display: flex;
        flex-direction: column;
        width: 100%;
        /*height: 300px;*/
    }

    .customer-info-block {
        display: flex;
        width: 100%;
        justify-content: space-between;

        /*background: red;*/
    }

    .customer-info-table-block {
        display: flex;
        width: 45%;
        flex-direction: column;
        margin-top: 1%;
        margin-left: 10%;
    }

    .customer-info-table {
        width: 100%;
        font-size: 10pt;
        background: white;

        /*border: 2px solid #ffc000;*/
    }

    .customer-info-table, td, th {
        border-collapse: collapse;
        border: 1px solid #ffc000;
    }

    .date-label {
        display: flex;
        width: 15%;
        height: 80px;
        flex-direction: column;
        justify-content: flex-start;
        margin-top: 1%;
        margin-right: 10%;
        align-items: flex-end;
        /*background: gold;*/

    }

    .date-label > h4 {
        /*background: #282c34;*/
    }

    .date-label > span {
        font-size: 10pt;
        /*background: #5ebd6d;*/
        text-decoration: underline;
        margin-top: -10%
    }

