        width: 100%;
        text-align: center;
        margin-top: 0.7%;
        /*background: #2196F3;*/
        font-size: 10pt;
        color: #161616;
        height: 25px;
    }

    .technovik-information-text {
        width: 100%;
        /*height: 50px;*/
        margin-top: -3%;
        padding: 0;
        /*background: red;*/
    }

    .technovik-information-text p {
        font-size: 10pt;
        margin: 0;
        font-weight: 500;
        color: black;
        letter-spacing: 0.3px;
        text-align: justify;
        /*background: #282c34;*/
    }

    .warehouses-info {
        display: flex;
        flex-direction: column;
        width: 80%;
        height: 200px;
        margin-left: 10%;

        /*background: gold;*/
    }

    .warehouse-info-title {
        margin-top: 0px;
        /*background: red;*/
        color: #002060;
    }

    .table-empty-cell {
        border: none;
    }

    /*.warehouses-info > */

    .warehouses-info table {
        margin-top: -2%;
    }


    /* выделение текста в параграфах */
    .blue-title {
        color: #002060;
        font-weight: bold;
        letter-spacing: 1px;
    }

    .par-tabul {
        margin-left: 25px;
    }

    .par-span-bold {
        font-weight: bold;
    }

    .par-span-underline {
        text-decoration: underline;
    }

