    }

    .description-and-price p {
        margin-top: -20px;
        font-size: 10pt;
        font-weight: 500;
        color: black;
        letter-spacing: 0.3px;
        /*text-align: justify;*/
    }

    .final-tables table {
        width: 100%;
        border-collapse: collapse;
    }

    .final-tables > tr, td {
        border: 1px solid #ffc000;
    }

    .final-tables table:first-child {
        margin-top: 10px;
    }

    .final-tables table:last-child {
        margin-top: 10px;
    }

    .final-tables p:last-child {

    }

    .manager-info {
        display: flex;
        width: 100%;
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-start;
        margin-top: -20px;

        /*background: gold;*/
    }

