        border-collapse: collapse;
    }

    .warehouse-passports-table td, th {
        text-align: center;
    }

    .warehouse-passports-table > table, td, th, tr {
        /*border-right: none;*/
    }

    #loading {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        padding-top: 100px;
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.7;
        background-color: #fff;
        z-index: 99;
        text-align: center;
    }

    #loading-image {
        /*left: 0;*/
        position: absolute;
        top: 20%;
        width: auto;
        height: 10%;
        z-index: 100;
    }

